<template>
	<div class="civil-front-page" v-if="newsAllList.length > 0">
		<div class="business-style-box">
			<div v-if="!$route.params.civilCategoryIndex" class="introduction-title-box" style="padding-bottom: 0.3rem">
				<div class="accounts-receivable-title">业务风采</div>
				<div class="accounts-receivable-line"></div>
			</div>

			<div v-else class="equity-title-box" style="margin: 0rem; padding: 0.2rem 0 0.3rem 0">
				<div class="equity-left-icon"></div>
				<div class="equity-right-title">业务风采</div>
			</div>

			<div class="news-list-box">
				<div class="news-list" v-for="(item, index) in newsAllList" :key="index">
					<div class="news-box" @click="toNewsDetails(item.uuid)">
						<div class="category-box">
							<span>{{ item.secCategoryName ? item.secCategoryName : item.categoryName}}</span>
						</div>
						<div class="news-time">
							<div class="time-day">{{ item.day }}</div>
							<div class="time-years">{{ item.years }}</div>
						</div>
						<div class="news-vertical"></div>
						<div class="title-intro">
							<div class="news-title">{{ item.title }}</div>
							<div class="news-intro">
								{{ item.summary }}
							</div>
						</div>
					</div>
					<div class="news-line"></div>
				</div>
			</div>
			<div class="page-box" v-if="newsAllList.length > 0">
				<pagination :total="totalCount" :page-size="params.rows" :current-page="params.page"
					@current-change="handleCurrentChange"></pagination>
			</div>
		</div>
	</div>
</template>

<script>
	import {
		newsList
	} from "../../../apis/openapi.js";
	import UTCUtil from "../../../../static/js/UTCUtil.js";
	import pagination from "@/components/pagination";
	export default {
		components: {
			pagination,
		},
		data() {
			return {
				totalCount: 100, //分页用的总数
				params: {
					page: 1,
					rows: 6,
					categoryUuid: "",
				},
				newsAllList: [],
			};
		},
		// 改变参数且不刷新页面
		watch: {
			// 监听参数是否发生变化
			$route: {
				handler() {
					if (this.$route.params.uuid) {
						this.params.secCategoryUuid = this.$route.params.uuid;
					} else {
						this.params.secCategoryUuid = "";
					}
					this.params.page = 1;
					this.newsList();
				},
				//深度监听，同时也可监听到param参数变化
				deep: true,
			},
		},
		mounted() {
			if (this.$store.state.categoryInfo) {
				let categoryList = this.$store.state.categoryInfo;
				this.params.categoryUuid = categoryList.find((item) => item.categoryName == '民商事').uuid
				if (this.$route.params.uuid) {
					this.params.secCategoryUuid = this.$route.params.uuid;
				}
				this.newsList()
			}
		},
		methods: {
			//查询新闻列表
			newsList() {
				this.params.secCategoryUuid = this.params.secCategoryUuid == '0' ? '' : this.params.secCategoryUuid
				newsList(this.params).then((res) => {
					if (res.data.success == "true") {
						this.newsAllList = res.data.data.data;
						this.totalCount = res.data.data.total;
						this.newsAllList.forEach((item) => {
							if (item.publishTime) {
								item.years = UTCUtil.showLocalDate(item.publishTime, "YYYY-mm");
								item.day = UTCUtil.showLocalDate(item.publishTime, "dd");
							}
						});
					}
				});
			},
			toNewsDetails(uuid) {
				this.$router.push('/news-details/'+uuid)
			},
			//分页切换
			handleCurrentChange(val) {
				this.params.page = val;
				this.newsList();
			},
		},
	};
</script>


<style lang="scss" scoped>
	.introduction-title-box {
		.accounts-receivable-title {
			font-size: 24px;
			font-family: SourceHanSansCN-Medium;
			font-weight: 500;
			line-height: 24px;
			color: #333333;
			letter-spacing: 0.48px;
			text-align: center;
			opacity: 1;
		}

		.accounts-receivable-line {
			width: 50px;
			height: 4px;
			background: #ab0101;
			opacity: 1;
			margin: 0 auto;
			margin-top: 20px;
		}

		.accounts-receivable-intro {
			font-size: 14px;
			font-family: SourceHanSansCN-Regular;
			font-weight: 400;
			line-height: 14px;
			color: #999999;
			letter-spacing: 0.28px;
			opacity: 1;
			text-align: center;
			margin-top: 14px;
		}
	}

	.equity-title-box {
		height: 24px;
		display: flex;
		justify-content: flex-start;

		margin-left: 8px;
		align-items: center;

		.equity-left-icon {
			width: 4px;
			height: 20px;
			background: #ab0101;
			opacity: 1;
		}

		.equity-right-title {
			height: 24px;
			font-size: 24px;
			font-family: SourceHanSansCN-Medium;
			font-weight: 500;
			line-height: 24px;
			color: #333333;
			letter-spacing: 0.48px;
			opacity: 1;
			margin-left: 14px;
		}
	}

	.business-style-box {
		width: 100%;
		margin: 0 auto;
		padding-top: 70px;
		box-sizing: border-box;

		.news-list-box {
			.news-list {
						.news-box {
							display: flex;
							align-items: center;
							//justify-content: space-between;
							margin-top: 40px;
							cursor: pointer;
							transition: all 0.4s;
							padding-left: 60px;
							box-sizing: border-box;
							.category-box{
								width: 33px;
								height: 43.5px;
								background-image: url(../../../../static/image/news-center/category-box.png);
								background-size: 100% 100%;
								background-repeat: no-repeat;
								margin-right: 30px;
								display: flex;
								align-items: center;
								justify-content: center;
								span{
									width: 25px;
									height: 30px;
									font-size: 12px;
									font-family: SourceHanSansCN-Medium;
									font-weight: 500;
									line-height: 18px;
									color: #AB0101;
									letter-spacing: 0.24px;
									text-align: center;
									opacity: 1;
									display: flex;
									align-items: center;
									justify-content: center;
								}
							}

							.title-intro {
								.news-title {
									width: 880px;
									height: 25px;
									font-size: 22px;
									font-family: SourceHanSansCN-Medium;
									font-weight: 400;
									line-height: 25px;
									color: #333333;
									opacity: 1;
									white-space: nowrap;
									text-overflow: ellipsis;
									overflow: hidden;
									word-break: break-all;
								}

								.news-intro {
									width: 870px;
									//height: 35px;
									font-size: 14px;
									font-family: SourceHanSansCN-Regular;
									font-weight: 400;
									line-height: 21px;
									color: #999999;
									opacity: 1;
									text-overflow: -o-ellipsis-lastline;
									overflow: hidden;
									text-overflow: ellipsis;
									display: -webkit-box;
									-webkit-line-clamp: 2;
									line-clamp: 2;
									-webkit-box-orient: vertical;
									margin-top: 20px;
								}
							}

							.news-vertical {
								height: 70px;
								border-left: 1px solid #DFDFDF;
								opacity: 1;
								margin-right: 40px;
							}

							.news-time {
								display: flex;
								flex-direction: column;
								align-items: center;
								margin-right: 40px;

								.time-day {
									height: 30px;
									font-size: 30px;
									font-family: SourceHanSansCN-Medium;
									font-weight: 500;
									line-height: 30px;
									color: #AB0101;
									letter-spacing: 0.60px;
									opacity: 1;
								}

								.time-years {
									height: 14px;
									font-size: 14px;
									font-family: SourceHanSansCN-Regular;
									font-weight: 400;
									line-height: 14px;
									color: #AB0101;
									letter-spacing: 0.28px;
									opacity: 1;
									margin-top: 13px;
								}
							}
						}

						.news-box:hover {
							.news-title {
								transition: all 0.4s;
								color: #AB0101;
							}
						}

						.news-line {
							width: 1200px;
							border-top: 1px dashed #DFDFDF;
							opacity: 1;
							margin-top: 30px;
						}
					}
		}

		.page-box {
			height: auto;
			display: flex;
			justify-content: center;
			margin-top: 40px;
		}
	}
</style>
