<template>
	<div class="contact-us">
		<div class="header-shadow"></div>
		<!-- start banner -->
		<div class="banner-box">
			<div class="banner-box-shadow"></div>
			<div class="banner-title-box">
				<div class="banner-title-top"></div>
				<div class="banner-title">网站地图</div>
				<div class="banner-title-bottom"></div>
			</div>
		</div>
		<!-- end banner -->
		<!-- start 网站地图 -->
		<div class="introduction-box" id="sitemap">
			<div class="introduction-middle">
				<div class="introduction-top-box">
					<div class="top-title">网站地图</div>
					<div class="top-left">
						<div class="top-left-text">SITE MAP</div>
						<div class="top-left-line"></div>
					</div>
				</div>
				<div class="sitemap-box" style="margin-top: 43px;">
					<div class="sitemap-title">关于有容</div>
					<div class="sitemap-text-box">
						<div @click="toggleNavigation(item.url)" class="sitemap-text"
							v-for="(item,index) in headerList">{{item.name}} </div>
					</div>
				</div>
				<div class="sitemap-line"></div>
				<div class="sitemap-box">
					<div class="sitemap-title">专业领域</div>
					<div class="sitemap-text-box">
						<div @click="toProfessional(item.uuid)" class="sitemap-text" v-for="(item,index) in fieldList">
							{{item.fieldName}}
						</div>
					</div>
				</div>
				<div class="sitemap-line"></div>
				<div class="sitemap-box">
					<div class="sitemap-title">民商事</div>
					<div class="sitemap-text-box">
						<div @click="toCivil(index,item.uuid)" class="sitemap-text"
							v-for="(item,index) in civilCategoryList">{{item.categoryName}} </div>
					</div>
				</div>
				<div class="sitemap-line"></div>
				<div class="sitemap-box">
					<div class="sitemap-title">法律知识</div>
					<div class="sitemap-text-box">
						<div @click="toNewsCenter(item)" class="sitemap-text" v-for="(item,index) in categoryAllList">
							{{item.categoryName}} </div>
					</div>
				</div>
				<div class="sitemap-line"></div>
			</div>
		</div>
		<!-- end 网站地图 -->
	</div>
</template>

<script>
	import {
		getContactUsList,
		getProfessionalFieldList
	} from '../../apis/openapi.js';
	import UTCUtil from '../../../static/js/UTCUtil.js';
	export default {
		components: {},
		data() {
			return {
				fieldList: [],
				categoryList: [],
				civilCategoryList: [],
				categoryAllList: [],
				headerList: [{
						name: '首页',
						url: '/'
					},
					{
						name: '应收账款',
						url: '/accounts-receivable'
					},
					{
						name: '法律顾问',
						url: '/legal-counsel'
					},
					{
						name: '刑事',
						url: '/criminal'
					},
					{
						name: '民商事',
						url: '/civil'
					},
					{
						name: '专业领域',
						url: '/professional-field'
					},
					{
						name: '法律知识',
						url: '/news-center'
					},
					{
						name: '联系我们',
						url: '/contact-us'
					},
				],
			}
		},
		mounted() {
			if (this.$store.state.categoryInfo) {
				this.categoryList = this.$store.state.categoryInfo
				this.civilCategoryList = this.categoryList.find((item) => item.categoryName == '民商事').children
				this.categoryAllList = [...this.categoryList, ...this.civilCategoryList]
			} else {
				this.getCategoryList()
			}
			if (this.$store.state.headFieldList) {
				this.fieldList = this.$store.state.headFieldList
			} else {
				this.getProfessionalFieldList()
			}
		},
		methods: {
			//查询分类
			getCategoryList() {
				let params = {
					page: 1,
					rows: 9999,
					categoryName: '',
					parentUuid: '0',
					categoryState: ''
				}
				getCategoryList(params).then(res => {
					if (res.data.success == 'true') {
						this.categoryList = res.data.data.data
						var categoryInfo = res.data.data.data
						this.$store.commit('modifyCategoryInfo', categoryInfo);
						localStorage.setItem('categoryInfo', JSON.stringify(categoryInfo))
						this.civilCategoryList = this.categoryList.find((item) => item.categoryName == '民商事')
							.children
					}
				})
			},
			//查询专业领域信息
			getProfessionalFieldList() {
				let headFieldList = []
				let params = {
					page: 1,
					rows: 100
				}
				getProfessionalFieldList(params).then(res => {
					if (res.data.success == 'true') {
						this.fieldList = res.data.data.data
						this.fieldList.forEach((item, key) => {
							headFieldList.push({
								fieldName: item.fieldName,
								sort: item.sort,
								uuid: item.uuid,
							})
						})
						this.$store.commit('modifyHeadFieldList', headFieldList);
						localStorage.setItem('headFieldList', JSON.stringify(headFieldList))
					}
				});
			},
			//跳转到专业邻域
			toProfessional(uuid) {
				this.$router.push('/professional-field-details/' + uuid)
			},
			//跳转到民商事
			toCivil(civilCategoryIndex, secCategoryUuid) {
				this.$router.push('/civil/' + civilCategoryIndex + '/' + secCategoryUuid)
			},
			//跳转到新闻中心
			toNewsCenter(item) {
				let categoryUuid = ''
				let secCategoryUuid = ''
				if (item.parentUuid != '') {
					categoryUuid = item.parentUuid
					secCategoryUuid = item.uuid
				} else {
					categoryUuid = item.uuid
				}
				this.$router.push({
					name: 'newsCenter',
					params: {
						categoryUuid: categoryUuid,
						secCategoryUuid: secCategoryUuid
					}
				});
			},
			//跳转分类（路由）
			toggleNavigation(url) {
				if (url == '/civil') {
					this.toCivil(-1, '0')
					return;
				}
				this.$router.push({
					path: url,
					query: {}
				});
			},

		}
	}
</script>
<style lang="scss" scoped>
	.contact-us {
		width: 100%;
		height: 100%;
	}

	.header-shadow {
		width: 100%;
		height: 80px;
		background: rgba(0, 0, 0, 0.2);
		position: absolute;
		top: 0;
		z-index: 5;
	}

	.banner-box {
		width: 100%;
		height: 360px;
		background-image: url(../../../static/image/sitemap/banner.png);
		background-size: cover;
		background-repeat: no-repeat;
		position: relative;
		padding-top: 174px;
		box-sizing: border-box;

		.banner-box-shadow {
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
			background: rgba(0, 0, 0, 0.2);
			z-index: 1;
		}

		.banner-title-box {
			width: 320px;
			height: 106px;
			margin: 0 auto;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			position: relative;
			z-index: 2;

			.banner-title-top {
				width: 150px;
				height: 3px;
				background-color: #FFFFFF;
				opacity: 1;
			}

			.banner-title {
				width: 100%;
				height: 46px;
				font-size: 46px;
				font-family: SourceHanSansCN-Medium;
				font-weight: 500;
				line-height: 46px;
				color: #FFFFFF;
				letter-spacing: 0.96px;
				opacity: 1;
				text-align: center;
			}

			.banner-title-bottom {
				width: 150px;
				height: 3px;
				background-color: #FFFFFF;
				opacity: 1;
				margin-left: auto;
			}
		}
	}

	.introduction-box {
		width: 100%;

		.introduction-middle {
			width: 1200px;
			height: 100%;
			min-height: 800px;
			margin: 0 auto;
			padding: 50px 0 100px 0;
			box-sizing: border-box;

			.introduction-top-box {
				display: flex;
				align-items: center;

				.top-title {
					//width: 125px;
					height: 30px;
					font-size: 30px;
					font-family: SourceHanSansCN-Medium;
					font-weight: 500;
					line-height: 30px;
					color: #333333;
					letter-spacing: 0.60px;
					opacity: 1;
				}

				.top-left {
					.top-left-text {
						//width: 101px;
						height: 24px;
						font-size: 20px;
						font-family: Roboto;
						font-weight: bold;
						line-height: 24px;
						color: #999999;
						letter-spacing: 0.40px;
						opacity: 1;
						margin-top: 4px;
						margin-left: 24px;
					}

					.top-left-line {
						width: 1060px;
						height: 2px;
						background-color: #DFDFDF;
