						margin-top: 2px;
						margin-left: 14px;
					}
				}
			}

			.sitemap-box {
				margin-top: 50px;
				margin-left: 20px;

				.sitemap-title {
					height: 22px;
					font-size: 22px;
					font-family: SourceHanSansCN-Medium;
					font-weight: 500;
					line-height: 22px;
					color: #333333;
					letter-spacing: 0.44px;
					opacity: 1;
				}

				.sitemap-text-box {
					width: 1150px;
					display: flex;
					flex-wrap: wrap;

					.sitemap-text {
						width: 230px;
						height: 18px;
						font-size: 18px;
						font-family: SourceHanSansCN-Regular;
						font-weight: 400;
						line-height: 18px;
						color: #666666;
						letter-spacing: 0.36px;
						opacity: 1;
						margin-top: 32px;
						cursor: pointer;
					}

					.sitemap-text:hover {
						color: #AB0101;
					}
				}
			}

			.sitemap-line {
				width: 1020px;
				height: 1px;
				background-image: linear-gradient(to right, #DFDFDF 0%, #DFDFDF 50%, transparent 50%);
				background-size: 15px 2px;
				background-repeat: repeat-x;
				opacity: 1;
				margin-top: 44px;
				margin-left: 90px;
			}
		}

	}
</style>
