<template>
	<div class="news-center">
		<div>
			<div class="header-shadow"></div>
			<!-- start banner -->
			<div class="banner-box">
				<div class="banner-box-shadow"></div>
				<div class="banner-title-box">
					<div class="banner-title-top"></div>
					<div class="banner-title">法律知识</div>
					<div class="banner-title-bottom"></div>
				</div>
			</div>
			<!-- end banner -->
		</div>
		<!-- start 有容简介 -->
		<div class="introduction-box">
			<div class="introduction-middle">
				<div class="introduction-top-box">
					<div class="top-title">法律知识</div>
					<div class="top-left">
						<div class="top-left-navmenu">
							<div class="top-left-text">NEWS CENTER</div>
						</div>
						<div class="top-left-line"></div>
					</div>
				</div>
				<div class="news-content">
					<div v-if="newsAllList.length>0" class="news-list-box">
						<div class="news-list" v-for="(item,index) in newsAllList">
							<div :style="{marginTop:index == 0 ?'0px':'40px'}" class="news-box"
								@click="toNewsDetails(item.uuid)">
								<div class="category-box">
									<span>{{item.secCategoryName?item.secCategoryName:item.categoryName}}</span>
								</div>
								<div class="news-time">
									<div class="time-day">{{item.day}}</div>
									<div class="time-years">{{item.years}}</div>
								</div>
								<div class="news-vertical"></div>
								<div class="title-intro">
									<div class="news-title">{{item.title}}</div>
									<div class="news-intro">
										{{item.summary}}
									</div>
								</div>
							</div>
							<div class="news-line"></div>
						</div>
					</div>
					<div class="page-box" v-if="newsAllList.length>0">
						<pagination :total="totalCount" :page-size="params.rows" :current-page="params.page"
							@current-change="handleCurrentChange"></pagination>
					</div>
					<div class="no-data" v-if="newsAllList.length == 0 && isShowNoData">
						<img src="../../../static/image/news-center-app/no-news.png" />
						<div class="no-data-text">暂无相关法律知识</div>
					</div>
				</div>
			</div>
		</div>
		<!-- end 有容简介 -->
	</div>
</template>

<script>
	import {
		newsList,
		getNewsDetail,
		getCategoryList
	} from '../../apis/openapi.js';
	import UTCUtil from '../../../static/js/UTCUtil.js';
	import pagination from '@/components/pagination';
	export default {
		metaInfo:window.$VueMetaInfo['newsCenter'],
		components: {
			pagination,
		},
		data() {
			return {
				isShowNoData:false,
				totalCount: 100, //分页用的总数
				newsAllList: [],
				params: {
					page: 1,
					rows: 6,
					categoryUuid: '',
					secCategoryUuid:''
				},

			}
		},
		mounted() {
			if (this.$route.params.categoryUuid){
				this.params.categoryUuid = this.$route.params.categoryUuid
			}
			if (this.$route.params.secCategoryUuid){
				this.params.secCategoryUuid = this.$route.params.secCategoryUuid
			}
			this.newsList()
		},
		methods: {
			//查询新闻列表
			newsList() {
				newsList(this.params).then(res => {
					if (res.data.success == 'true') {
						this.newsAllList = res.data.data.data
						this.totalCount = res.data.data.total
						this.newsAllList.forEach(item => {
							if (item.publishTime) {
								item.years = UTCUtil.showLocalDate(item.publishTime, 'YYYY-mm');
								item.day = UTCUtil.showLocalDate(item.publishTime, 'dd');
							}
						})
						this.isShowNoData = true
					}
				});
			},
			//跳转到新闻详情
			toNewsDetails(uuid) {
				this.$router.push('/news-details/'+uuid)
			},
			//分页切换
			handleCurrentChange(val) {
				this.params.page = val;
				this.newsList()
				document.body.scrollTop = 0; // chrome
				document.documentElement.scrollTop = 0; // firefox
			},
			//跳转分类（路由）
			toggleNavigation(url, index) {
				this.$router.push({
					path: url,
					query: {}
				});
				//this.$forceUpdate()
			},

		}
	}
</script>
