		methods: {
			//查询专业领域信息
			getProfessionalFieldList() {
				let params = {
					page:1,
					rows:100
				}
				getProfessionalFieldList(params).then(res => {
					if (res.data.success == 'true') {
						this.fieldList = res.data.data.data
					}
				});
			},
			toDetails(index,uuid) {
				getProfessionFieldDetail({uuid:uuid}).then(res => {
					if (res.data.success == 'true') {
						this.fieldDetails = res.data.data
						this.showDetails = false
						this.fieldIndex = index
						document.body.scrollTop = 0; // chrome
						document.documentElement.scrollTop = 0; // firefox
					}
				});
			},
			//跳转到专业邻域(该页面跳转是为了刷新路由传值)
