<script>
	import {
		newsList,
		getNewsDetail,
	} from '../../apis/openapi.js';
	import UTCUtil from '../../../static/js/UTCUtil.js';
	export default {
		metaInfo(){
		  return {
		    title:this.newsDetail.title + '- 法律知识 ｜ 有容律所',
		    meta: [{
		      name: 'keyWords',
		      content: '法律知识'
		    },
		    {
		      name: 'description',
		      content: '山东有容律师事务所，是一家综合性、一站式的专业法律服务机构，致力于为客户提供全方位、高效、专业的法律服务，业务领域涵盖：婚姻遗产、劳动纠纷等民商事诉讼与仲裁，刑事辩护、刑事法律风险防范等刑事法律服务、应收帐款代理回款及合同纠纷、公司法、债券债务等方面的法律顾问服务。' 
		    }]
		  }
		},
		components: {
		},
		data() {
			return {
				categoryList: [],
				newsDetail: {}, //新闻详情

			}
		},
		mounted() {
			if (this.$route.params.uuid){
				this.toDetails(this.$route.params.uuid)
			}
		},
		methods: {
			//查询新闻分类
			getCategoryList() {
				return new Promise((resolve, reject) => {
					let params = {
						page: 1,
						rows: 9999,
						categoryName: '',
						parentUuid: '0',
						categoryState: ''
					}
					getCategoryList(params).then(res => {
						if (res.data.success == 'true') {
							this.categoryList = res.data.data.data
							resolve()
						}
					})
				});
			},
			//新闻详情
			toDetails(uuid) {
				getNewsDetail({
					uuid: uuid
				}).then(res => {
					if (res.data.success == 'true') {
						this.newsDetail = res.data.data
						if (this.newsDetail.publishTime) {
							this.newsDetail.publishTime = UTCUtil.showLocalDate(this.newsDetail
								.publishTime, 'YYYY-mm-dd');
						}
						// document.body.scrollTop = 0; // chrome
						// document.documentElement.scrollTop = 0; // firefox
					}
				});
			},
			//跳转分类（路由）
			toggleNavigation(url, index) {
				this.$router.push({
					path: url,
					query: {}
				});
				//this.$forceUpdate()
			},

		}
	}
</script>
