<template>
	<div class="news-center-app">
		<!-- start banner -->
		<div class="banner-box" v-if="showDetails">
			<div class="banner-box-shadow"></div>
			<div class="banner-title-box">
				<div class="banner-title-top"></div>
				<div class="banner-title">法律知识</div>
				<div class="banner-title-bottom"></div>
			</div>
		</div>
		<div style="width: 100%;height: 1.1rem;" v-else></div>
		<!-- end banner -->

		<!-- start 法律知识 -->
		<div class="introduction-box">
			<div class="introduction-middle">
				<div v-if="showDetails" class="introduction-top-box">
					<div class="top-title">法律知识</div>
					<div class="top-left">
						<div class="top-left-text">NEWS CENTER</div>
						<div class="top-left-line"></div>
					</div>
				</div>
				<div v-else class="navmenu-top-box">
					<div class="navmenu-box">
						<div class="navmenu-details"  @click.stop="toggleNavigation('/homeApp')">首页</div>
						<img src="../../../static/image/professional-field/arrow.png" />
						<div class="navmenu-details" @click.stop="toggleNavigation('/news-center-app')">法律知识</div>
						<img src="../../../static/image/professional-field/arrow.png" />
						<!-- <div class="navmenu-details" @click.stop="chooseCategory(item.uuid)" v-for="(item,index) in categoryList" v-if="newsDetail.categoryUuid==item.uuid">{{item.categoryName}}</div>
						<img src="../../../static/image/professional-field/nabar.png" /> -->
						<div class="navmenu-details" style="color: #999999;">正文</div>
					</div>
				</div>
				<div class="news-list-box" v-if="showDetails">
					<!-- <div class="category-box">
						<div class="category-text" v-for="(item,index) in categoryList" v-if="params.categoryUuid == item.uuid">{{item.categoryName}}</div>
						<div class="category-line"></div>
					</div> -->
					<div style="width: 100%; height: 0.3rem;"></div>
					<div v-if="newsAllList.length>0" @click="toNewsCenter(item.uuid)" class="news-list" v-for="(item,index) in newsAllList">
						<div class="news-time-box">
							<div class="category-border">
								<span>{{item.secCategoryName?item.secCategoryName:item.categoryName}}</span>
							</div>
							<div class="news-time">
								<div class="time-day">{{item.day}}</div>
								<div class="time-years">{{item.years}}</div>
							</div>
						</div>
						<div class="news-title">{{item.title}}</div>
						<div class="news-intro">{{item.summary}}</div>
					    <div class="news-line"></div>
					</div>
					<div class="page-box" v-if="newsAllList.length>0">
						<pagination :small="true" :total="totalCount" :page-size="params.rows" :current-page="params.page"
							@current-change="handleCurrentChange"></pagination>
					</div>
					<div class="no-data" v-if="newsAllList.length == 0 && isShowNoData">
						<img src="../../../static/image/news-center-app/no-news.png" />
						<div class="no-data-text">暂无相关法律知识</div>
					</div>
				</div>
				<div class="news-details" v-else>
					<div class="news-details-title" v-if="newsDetail.title">{{newsDetail.title}}</div>
					<div class="news-details-text" v-html="newsDetail.content"></div>
					<!-- <div class="news-details-author" v-if="newsDetail.newsAuthor">作者：{{newsDetail.newsAuthor}}</div> -->
					<div class="news-details-time" v-if="newsDetail.publishTime">发布时间：{{newsDetail.publishTime}}</div>
				</div> 
			</div>
		</div>

		<!-- end 法律知识 -->

	</div>
</template>

<script>
	import {
		newsList,
		getNewsDetail,
		getCategoryList
	} from '../../apis/openapi.js';
	import UTCUtil from '../../../static/js/UTCUtil.js';
	import pagination from '@/components/pagination';
	export default {
		components: {
			pagination
		},
		data() {
			return {
				isShowNoData:false,
				showDetails: true,
				totalCount: 100, //分页用的总数
				newsAllList:[],
				categoryList:[],
				newsDetail: {
					content: "<p style=\"padding: 0px; margin-top: 0px; margin-bottom: 20px; text-indent: 2em; color: rgb(34, 34, 34); font-family: &quot;Microsoft YaHei&quot;, 微软雅黑, 宋体; white-space: normal; background-color: rgb(255, 255, 255); line-height: 24px;\"><span style=\"padding: 0px; margin: 0px; line-height: 24px; font-family: 宋体;\"><span style=\"padding: 0px; margin: 0px;\"><span style=\"padding: 0px; margin: 0px; line-height: 24px;\"><span style=\"padding: 0px; margin: 0px;\">5月2</span></span><span style=\"padding: 0px; margin: 0px; line-height: 24px;\"><span style=\"padding: 0px; margin: 0px;\">4</span></span><span style=\"padding: 0px; margin: 0px; line-height: 24px;\"><span style=\"padding: 0px; margin: 0px;\">日下午，中国侨联文化交流部、北京语言大学汉语国际教育研究院（华文教育研究院）、</span></span><span style=\"padding: 0px; margin: 0px; line-height: 24px;\"><span style=\"padding: 0px; margin: 0px;\">人文社会科学学部</span></span><span style=\"padding: 0px; margin: 0px; line-height: 24px;\"><span style=\"padding: 0px; margin: 0px;\">、梅蘭书院在北语召开华文教育座谈会，并启动侨社志愿者（</span><span style=\"padding: 0px; margin: 0px;\">Chinese Community Volunteers，简称CCV）招募工作，为“亲情中华·为你讲故事”夏（春、秋、冬）令营活动提供志愿者服务支持。中国侨联文化</span></span><span style=\"padding: 0px; margin: 0px; line-height: 24px;\"><span style=\"padding: 0px; margin: 0px;\">交流部</span></span><span style=\"padding: 0px; margin: 0px; line-height: 24px;\"><span style=\"padding: 0px; margin: 0px;\">教育处处长孙刚，北京语言大学华文教育研究院名誉院长李晓梅，汉语国际教育</span></span><span style=\"padding: 0px; margin: 0px; line-height: 24px;\"><span style=\"padding: 0px; margin: 0px;\">研究院</span></span><span style=\"padding: 0px; margin: 0px; line-height: 24px;\"><span style=\"padding: 0px; margin: 0px;\">院长</span></span><span style=\"padding: 0px; margin: 0px; line-height: 24px;\"><span style=\"padding: 0px; margin: 0px;\">吴应辉</span></span><span style=\"padding: 0px; margin: 0px; line-height: 24px;\"><span style=\"padding: 0px; margin: 0px;\">、</span></span><span style=\"padding: 0px; margin: 0px; line-height: 24px;\"><span style=\"padding: 0px; margin: 0px;\">人文社会科学学部主任程娟</span></span><span style=\"padding: 0px; margin: 0px; line-height: 24px;\"><span style=\"padding: 0px; margin: 0px;\">、</span></span><span style=\"padding: 0px; margin: 0px; line-height: 24px;\"><span style=\"padding: 0px; margin: 0px;\">党委副书记卜晨光</span></span><span style=\"padding: 0px; margin: 0px; line-height: 24px;\"><span style=\"padding: 0px; margin: 0px;\">、教授代表冯惟钢、梅蘭书院院长蒋燕等嘉宾以及</span><span style=\"padding: 0px; margin: 0px;\">50余名志愿者代表出席交流活动。活动由</span></span><span style=\"padding: 0px; margin: 0px; line-height: 24px;\"><span style=\"padding: 0px; margin: 0px;\">人文社会科学</span></span><span style=\"padding: 0px; margin: 0px; line-height: 24px;\"><span style=\"padding: 0px; margin: 0px;\">学部副主任施家炜主持，分为华文教育座谈会与侨社志愿者宣讲会两部分。</span></span></span></span></p><p style=\"padding: 0px; margin-top: 0px; margin-bottom: 20px;  text-align: center; color: rgb(34, 34, 34); font-family: &quot;Microsoft YaHei&quot;, 微软雅黑, 宋体; white-space: normal; background-color: rgb(255, 255, 255); line-height: 24px;\"><br/></p><p style=\"padding: 0px; margin-top: 0px; margin-bottom: 20px; text-align: center; color: rgb(34, 34, 34); font-family: &quot;Microsoft YaHei&quot;, 微软雅黑, 宋体; white-space: normal; background-color: rgb(255, 255, 255); line-height: 24px;\"><span style=\"padding: 0px; margin: 0px; line-height: 24px; font-family: 宋体;\"><img src=\"https://crowdedu.coocedu.com/LMSOSS/news/2021/05/6ee38676-8629-4ae0-93ef-abd213447b4b.jpg\" title=\"LMSOSS/news/2021/05/6ee38676-8629-4ae0-93ef-abd213447b4b.jpg\" style=\"white-space: normal;\"/></span></p><p style=\"padding: 0px; margin-top: 0px; margin-bottom: 20px;  color: rgb(34, 34, 34); font-family: &quot;Microsoft YaHei&quot;, 微软雅黑, 宋体; white-space: normal; background-color: rgb(255, 255, 255); line-height: 24px; text-align: center;\"><span style=\"padding: 0px; margin: 0px; line-height: 24px; font-family: 宋体;\">座谈会现场</span></p><p style=\"padding: 0px; margin-top: 0px; margin-bottom: 20px;  text-align: center; color: rgb(34, 34, 34); font-family: &quot;Microsoft YaHei&quot;, 微软雅黑, 宋体; white-space: normal; background-color: rgb(255, 255, 255); line-height: 24px;\"><br/></p><p style=\"padding: 0px; margin-top: 0px; margin-bottom: 20px; text-align: center; color: rgb(34, 34, 34); font-family: &quot;Microsoft YaHei&quot;, 微软雅黑, 宋体; white-space: normal; background-color: rgb(255, 255, 255); line-height: 24px;\"><span style=\"padding: 0px; margin: 0px; line-height: 24px; font-family: 宋体;\"><img src=\"https://crowdedu.coocedu.com/LMSOSS/news/2021/05/5d681dd7-a246-4507-aefd-b0c962c81361.jpg\" title=\"LMSOSS/news/2021/05/5d681dd7-a246-4507-aefd-b0c962c81361.jpg\" style=\"white-space: normal;\"/></span></p><p style=\"padding: 0px; margin-top: 0px; margin-bottom: 20px; text-indent: 2em; color: rgb(34, 34, 34); font-family: &quot;Microsoft YaHei&quot;, 微软雅黑, 宋体; white-space: normal; background-color: rgb(255, 255, 255); line-height: 24px; text-align: center;\"><span style=\"padding: 0px; margin: 0px; line-height: 24px; font-family: 宋体;\">座谈嘉宾合影</span></p><p style=\"padding: 0px; margin-top: 0px; margin-bottom: 20px; text-indent: 2em; text-align: center; color: rgb(34, 34, 34); font-family: &quot;Microsoft YaHei&quot;, 微软雅黑, 宋体; white-space: normal; background-color: rgb(255, 255, 255); line-height: 24px;\"><a href=\"http://news.blcu.edu.cn/__local/D/CB/26/B3656232913864D09575B619D4C_8152D95E_1C276E.png\" target=\"_blank\" style=\"padding: 0px; margin: 0px; text-decoration-line: none; color: rgb(34, 34, 34); transition: all 0.5s ease 0s;\"></a></p><p><br/></p><p><br/></p><p style=\"text-align: center;\"><img src=\"https://crowdedu.coocedu.com/LMSOSS/news/2021/05/163e4159-8ce4-4298-b4ba-e401cfbd7b61.jpg\" style=\"\" title=\"LMSOSS/news/2021/05/163e4159-8ce4-4298-b4ba-e401cfbd7b61.jpg\"/></p><p style=\"padding: 0px; margin-top: 0px; margin-bottom: 20px; text-indent: 2em; text-align: center; color: rgb(34, 34, 34); font-family: &quot;Microsoft YaHei&quot;, 微软雅黑, 宋体; white-space: normal; background-color: rgb(255, 255, 255); line-height: 24px;\"><a href=\"http://news.blcu.edu.cn/__local/D/CB/26/B3656232913864D09575B619D4C_8152D95E_1C276E.png\" target=\"_blank\" style=\"padding: 0px; margin: 0px; text-decoration-line: none; color: rgb(34, 34, 34); transition: all 0.5s ease 0s;\"></a><br/></p><p style=\"padding: 0px; margin-top: 0px; margin-bottom: 20px; text-indent: 2em; text-align: center; color: rgb(34, 34, 34); font-family: &quot;Microsoft YaHei&quot;, 微软雅黑, 宋体; white-space: normal; background-color: rgb(255, 255, 255); line-height: 24px;\"><span style=\"padding: 0px; margin: 0px; line-height: 24px; font-family: 宋体;\">北语学生认真听取参与志愿者工作的介绍</span></p><p style=\"padding: 0px; margin-top: 0px; margin-bottom: 20px; text-indent: 32px; text-align: justify; color: rgb(34, 34, 34); font-family: &quot;Microsoft YaHei&quot;, 微软雅黑, 宋体; white-space: normal; background-color: rgb(255, 255, 255); line-height: 24px;\"><span style=\"padding: 0px; margin: 0px; line-height: 24px; font-family: 宋体;\"><span style=\"padding: 0px; margin: 0px;\">李晓梅院长表示，海外侨社和华校是传播中华文化的重要力量，是讲好中国故事、传递中国声音的重要渠道，北京语言大学作为汉语国际教育的领军高校、梅蘭书院作为部级课题</span><span style=\"padding: 0px; margin: 0px;\">“区块链+华文教育研究”的海外实践基地，支持侨社建设、支持华校建设是义不容辞的责任，将全面开启志愿者平台为海外侨社和华社做好服务工作，首批志愿者将为中国侨联的“亲情中华·为你讲故事”夏（春、秋、冬）令营活动提供服务，接下来北语和梅蘭书院将根据学生的专业特长进行分类，为海外华校提供语言、朗诵、书法、舞蹈、武术等方面的咨询和教学服务，学生将在志愿者平台上学以致用，了解侨情，增强社会实践经验，志愿者服务将助力高校学生的全面发展。</span></span></p><p style=\"padding: 0px; margin-top: 0px; margin-bottom: 20px; text-indent: 32px; text-align: justify; color: rgb(34, 34, 34); font-family: &quot;Microsoft YaHei&quot;, 微软雅黑, 宋体; white-space: normal; background-color: rgb(255, 255, 255); line-height: 24px;\"><span style=\"padding: 0px; margin: 0px; line-height: 24px; font-family: 宋体;\"><span style=\"padding: 0px; margin: 0px;\">孙刚处长在发言中说，华文教育对于海外华侨华人社会发展具有重大意义。他认为，华文教育既要以教授中文和做好中华文化在海外侨社的传承作为主要任务，也要以侨为</span><span style=\"padding: 0px; margin: 0px;\">“桥”来实现中国人民与各国人民的心灵相通，推动世界文明交流互鉴，构建人类命运共同体。</span></span></p><p style=\"padding: 0px; margin-top: 0px; margin-bottom: 20px; text-indent: 32px; text-align: justify; color: rgb(34, 34, 34); font-family: &quot;Microsoft YaHei&quot;, 微软雅黑, 宋体; white-space: normal; background-color: rgb(255, 255, 255); line-height: 24px;\"><span style=\"padding: 0px; margin: 0px; line-height: 24px; font-family: 宋体;\">吴应辉院长充分肯定了本次合作，认为其具有三方面的重要意义：首先，华文教育是汉语国际教育的重要组成部分；其次，华文教育增强了华人华侨对国家的认同感，能够提高对中国形象美好度的认知；第三，本次志愿者项目能够为汉语国际教育学子提供实践机会，增加对海外华人华侨的了解，在实践中检验专业能力。</span></p><p style=\"padding: 0px; margin-top: 0px; margin-bottom: 20px; text-indent: 32px; text-align: justify; color: rgb(34, 34, 34); font-family: &quot;Microsoft YaHei&quot;, 微软雅黑, 宋体; white-space: normal; background-color: rgb(255, 255, 255); line-height: 24px;\"><span style=\"padding: 0px; margin: 0px; line-height: 24px; font-family: 宋体;\">程娟主任高度肯定了华文教育的重大影响力，并从组织架构、学生培养、学科建设、师资队伍等方面向与会嘉宾介绍了学部基本情况，突出了学校在汉语国际教育领域的旗舰作用，期待志愿者合作项目能够取得圆满成功。</span></p><p style=\"padding: 0px; margin-top: 0px; margin-bottom: 20px; text-indent: 38px; text-align: justify; color: rgb(34, 34, 34); font-family: &quot;Microsoft YaHei&quot;, 微软雅黑, 宋体; white-space: normal; background-color: rgb(255, 255, 255); line-height: 24px;\"><span style=\"padding: 0px; margin: 0px; line-height: 24px; font-family: 宋体;\">卜晨光副书记谈到新媒体传播对华文教育的重要作用。大家就加强传播方面的合作交流形成共识，并表达未来在相关领域的合作意向。在交流过程中，各方均强调了线上线下合作学习交流的重要作用。</span></p><p style=\"padding: 0px; margin-top: 0px; margin-bottom: 20px; text-indent: 32px; text-align: justify; color: rgb(34, 34, 34); font-family: &quot;Microsoft YaHei&quot;, 微软雅黑, 宋体; white-space: normal; background-color: rgb(255, 255, 255); line-height: 24px;\"><span style=\"padding: 0px; margin: 0px; line-height: 24px; font-family: 宋体;\"><span style=\"padding: 0px; margin: 0px;\">在随后与北语志愿者代表的交流会上，李晓梅院长和孙刚处长向同学们介绍了华文教育和侨社志愿者工作的开展情况和基本理念。中国侨联文化交流部教育处干部李一鸣女士介绍了</span><span style=\"padding: 0px; margin: 0px;\">“亲情中华·为你讲故事”夏（春、秋、冬）令营志愿者项目的具体组织形式和活动安排。</span></span></p><p style=\"padding: 0px; margin-top: 0px; margin-bottom: 20px; text-indent: 32px; text-align: justify; color: rgb(34, 34, 34); font-family: &quot;Microsoft YaHei&quot;, 微软雅黑, 宋体; white-space: normal; background-color: rgb(255, 255, 255); line-height: 24px;\"><span style=\"padding: 0px; margin: 0px; line-height: 24px; font-family: 宋体;\">施家炜</span><span style=\"padding: 0px; margin: 0px; line-height: 24px; font-family: 宋体;\">副主任最后总结到</span><span style=\"padding: 0px; margin: 0px; line-height: 24px; font-family: 宋体;\"><span style=\"padding: 0px; margin: 0px;\">，华文教育作为国际中文教育的重要组成部分，不仅涉及中华文化的传播和传承，在学科研究层面也具有重大意义，既是</span><span style=\"padding: 0px; margin: 0px;\">“留根工程”，也是“希望工程”，是一项有情怀的事业。华文教育志愿者项目不仅有助于学生自身的能力发展，更促进了国际交流与文化传播。参加本次交流活动的各方将进一步深化项目合作，进一步服务到侨务和国际交流中，为讲好中国故事、传播中国文化贡献力量。</span></span></p><p><br/></p>"
				}, //新闻详情
				params: {
					page: 1,
					rows: 6,
					categoryUuid:''
				},

			}
		},
		watch: {
			$route: { // 用来监听query参数变化
				handler() {
					if (this.$route.query.newsUuid) {
						this.newsDetail ={}
						this.showDetails = false;
						this.toDetails(this.$route.query.newsUuid)
					}else{
						this.params.page = 1;
						this.showDetails = true;
						this.newsList()
					}
					//深度监听，同时也可监听到param参数变化
				},
				deep: true,
			}
		},
		mounted() {
			if (this.$route.params.categoryUuid){
				this.params.categoryUuid = this.$route.params.categoryUuid
			}
			if (this.$route.params.secCategoryUuid){
				this.params.secCategoryUuid = this.$route.params.secCategoryUuid
			}
			if (this.$route.query.newsUuid) {
				this.newsDetail ={}
				this.showDetails = false;
				this.toDetails(this.$route.query.newsUuid)
			}
			this.newsList()
			this.getCategoryList()

		},
		methods: {
			//查询新闻分类
			getCategoryList() {
				let params = {
					page: 1,
					rows: 9999,
					categoryName: '',
					parentUuid: '0',
					categoryState: ''
				}
				getCategoryList(params).then(res => {
					if (res.data.success == 'true') {
						this.categoryList = res.data.data.data
					}
				})
			},
			//查询新闻列表
			newsList() {
				newsList(this.params).then(res => {
					if (res.data.success == 'true') {
						this.newsAllList = res.data.data.data
						this.totalCount = res.data.data.total
						this.newsAllList.forEach(item => {
							if (item.publishTime) {
								item.years = UTCUtil.showLocalDate(item.publishTime, 'YYYY/mm');
								item.day = UTCUtil.showLocalDate(item.publishTime, 'dd');
							}
						})
						this.isShowNoData = true
					}
				});
			},
			chooseCategory(uuid){
				this.params.page = 1;
				this.params.categoryUuid =uuid
				this.showDetails = true;
				this.newsList()
			},
			//跳转到法律知识(该页面跳转是为了刷新路由传值)
			toNewsCenter(uuid) {
				this.$router.push({
					path: '/news-center-app',
					query: {
						newsUuid: uuid
					}
				});
			},
			toDetails(uuid){
				getNewsDetail({uuid:uuid}).then(res => {
					if (res.data.success == 'true') {
						this.newsDetail = res.data.data
						if (this.newsDetail.publishTime) {
							this.newsDetail.publishTime = UTCUtil.showLocalDate(this.newsDetail.publishTime, 'YYYY-mm-dd');
						}
						this.showDetails = false;
						document.body.scrollTop = 0; // chrome
						document.documentElement.scrollTop = 0; // firefox
					}
				});
			},
			//分页切换
			handleCurrentChange(val) {
				this.params.page = val;
				this.newsList()
				document.body.scrollTop = 0; // chrome
				document.documentElement.scrollTop = 0; // firefox
			},
