<script>
	import {
		newsList,
	} from "../../../apis/openapi.js";
	import UTCUtil from "../../../../static/js/UTCUtil.js";
	import pagination from "@/components/pagination";
	export default {
		components: {
			pagination,
		},
		data() {
			return {
				scroll: 0,
				totalCount: 100, //分页用的总数
				params: {
					page: 1,
					rows: 6,
					categoryUuid: "",
				},
				categoryList: [],
				civilCategoryIndex: -1, //首页分类下标
				civilCategoryStyle: 0, //首页分类样式下标
				serviceModelStyle: 0, //首页服务模式样式下标
				secondCategoryList: [],
				secondCategoryStyle: [{
						defaultImg: require("../../../../static/image/civil/contractual-dispute.png"),
						hoverImg: require("../../../../static/image/civil/contractual-dispute-hover.png"),
					},
					{
						defaultImg: require("../../../../static/image/civil/marital-inheritance.png"),
						hoverImg: require("../../../../static/image/civil/marital-inheritance-hover.png"),
					},
					{
						defaultImg: require("../../../../static/image/civil/infringement-disputes.png"),
						hoverImg: require("../../../../static/image/civil/infringement-disputes-hover.png"),
					},
					{
						defaultImg: require("../../../../static/image/civil/labor-dispute.png"),
						hoverImg: require("../../../../static/image/civil/labor-dispute-hover.png"),
					},
					{
						defaultImg: require("../../../../static/image/civil/equity.png"),
						hoverImg: require("../../../../static/image/civil/equity-hover.png"),
					},
				],
			};
		},
		computed: {
			isScroll() {
				return this.$store.state.scroll; //需要监听的数据
			},
		},
		// 改变参数且不刷新页面
		watch: {
			// 监听参数是否发生变化
			$route: {
				handler() {
					if (this.$route.params.civilCategoryIndex) {
						this.civilCategoryIndex = this.$route.params.civilCategoryIndex;
					} else {
						this.civilCategoryIndex = -1;
					}
				},
				//深度监听，同时也可监听到param参数变化
				deep: true,
			},
			//监听页面是否滚动
			isScroll(newVal, oldVal) {
				this.scroll = newVal;
			},
		},
		mounted() {
			if (this.$store.state.categoryInfo) {
				let categoryList = this.$store.state.categoryInfo;
				this.secondCategoryList = categoryList.find((item) => item.categoryName == "民商事").children;
				this.params.categoryUuid = categoryList.find((item) => item.categoryName == '民商事').uuid
			}
			if (this.$route.params.civilCategoryIndex) {
				this.civilCategoryIndex = this.$route.params.civilCategoryIndex;
			}
		},
		methods: {
			handleMouseEnter(index) {
				this.civilCategoryStyle = index;
			},
			handleMouseLeave(index) {
				//this.civilCategoryStyle = -1
			},
			ServiceModeEnter(index) {
				this.serviceModelStyle = index;
			},
			ServiceModeLeave(index) {
				//this.serviceModelStyle = -1
			},
			//跳转到民商事(该页面跳转是为了刷新路由传值)
			toCivil(civilCategoryIndex, secCategoryUuid) {
				this.$router.push('/civil/'+civilCategoryIndex+'/'+secCategoryUuid)
			},
			//跳转分类（路由）
			toggleNavigation(url, index) {
				this.$router.push({
					path: url,
					query: {},
				});
				//this.$forceUpdate()
			},
		},
	};
</script>


