<template>
	<div class="contact-us-app">
		<!-- start banner -->
		<div class="banner-box">
			<div class="banner-box-shadow"></div>
			<div class="banner-title-box">
				<div class="banner-title-top"></div>
				<div class="banner-title">网站地图</div>
				<div class="banner-title-bottom"></div>
			</div>
		</div>
		<!-- end banner -->

		<!-- start 有容简介 -->
		<div class="introduction-box">
			<div class="introduction-middle">
				<div class="introduction-padding">
					<div class="introduction-top-box">
						<div class="top-title">网站地图</div>
						<div class="top-left">
							<div class="top-left-text">SITE MAP</div>
							<div class="top-left-line"></div>
						</div>
					</div>
					<div class="sitemap-box" style="margin-top: 0.6rem;">
						<div class="sitemap-title">关于有容</div>
						<div class="sitemap-text-box">
							<div @click="toggleNavigation(item.url)" class="sitemap-text"
								v-for="(item,index) in headerList">{{item.name}} </div>
						</div>
					</div>
					<div class="sitemap-line"></div>
					<div class="sitemap-box">
						<div class="sitemap-title">专业领域</div>
						<div class="sitemap-text-box">
							<div @click="toProfessional(index,item.uuid)" class="sitemap-text"
								v-for="(item,index) in fieldList">
								{{item.fieldName}}
							</div>
						</div>
					</div>
					<div class="sitemap-line"></div>
					<div class="sitemap-box">
						<div class="sitemap-title">民商事</div>
						<div class="sitemap-text-box">
							<div @click="toCivilApp(index,item.uuid)" class="sitemap-text"
								v-for="(item,index) in civilCategoryList">{{item.categoryName}} </div>
						</div>
					</div>
					<div class="sitemap-line"></div>
					<div class="sitemap-box">
						<div class="sitemap-title">法律知识</div>
						<div class="sitemap-text-box">
							<div @click="toNewsCenter(item)" class="sitemap-text"
								v-for="(item,index) in categoryAllList">
								{{item.categoryName}}
							</div>
						</div>
					</div>
					<div class="sitemap-line"></div>
				</div>
			</div>
		</div>

		<!-- end 有容简介 -->


	</div>
</template>

<script>
	import {
		getContactUsList,
	} from '../../apis/openapi.js';
	export default {
		components: {},
		data() {
			return {
				fieldList: [],
				categoryList: [],
				civilCategoryList: [],
				categoryAllList: [],
				headerList: [{
						name: '首页',
						url: '/homeApp'
					},
					{
						name: '应收账款',
						url: '/accounts-receivable-app'
					},
					{
						name: '法律顾问',
						url: '/legal-counsel-app'
					},
					{
						name: '刑事',
						url: '/criminal-app'
					},
					{
						name: '民商事',
						url: '/civil-app'
					},
					{
						name: '专业领域',
						url: '/professional-field-app'
					},
					{
						name: '法律知识',
						url: '/news-center-app'
					},
					{
						name: '联系我们',
						url: '/contact-us-app'
					},
				],
			}
		},
		mounted() {
			if (this.$store.state.categoryInfo) {
				this.categoryList = this.$store.state.categoryInfo
				this.civilCategoryList = this.categoryList.find((item) => item.categoryName == '民商事').children
				this.categoryAllList = [...this.categoryList, ...this.civilCategoryList]
			} else {
				this.getCategoryList()
			}
			if (this.$store.state.headFieldList) {
				this.fieldList = this.$store.state.headFieldList
			} else {
				this.getProfessionalFieldList()
			}
		},
		methods: {
			//查询分类
			getCategoryList() {
				let params = {
					page: 1,
					rows: 9999,
					categoryName: '',
					parentUuid: '0',
					categoryState: ''
				}
				getCategoryList(params).then(res => {
					if (res.data.success == 'true') {
						this.categoryList = res.data.data.data
						var categoryInfo = res.data.data.data
						this.$store.commit('modifyCategoryInfo', categoryInfo);
						localStorage.setItem('categoryInfo', JSON.stringify(categoryInfo))
						this.civilCategoryList = this.categoryList.find((item) => item.categoryName == '民商事')
							.children
					}
				})
			},
			//查询专业领域信息
			getProfessionalFieldList() {
				let headFieldList = []
				let params = {
					page: 1,
					rows: 100
				}
				getProfessionalFieldList(params).then(res => {
					if (res.data.success == 'true') {
						this.fieldList = res.data.data.data
						this.fieldList.forEach((item, key) => {
							headFieldList.push({
								fieldName: item.fieldName,
								sort: item.sort,
								uuid: item.uuid,
							})
						})
						this.$store.commit('modifyHeadFieldList', headFieldList);
						localStorage.setItem('headFieldList', JSON.stringify(headFieldList))
					}
				});
			},
			//跳转到专业邻域
			toProfessional(index,uuid) {
				this.$router.push({
					path: '/professional-field-app',
					query: {
						fieldIndex:index,
						fieldUuid: uuid,
					}
				});
			},
			//跳转到民商事
			toCivilApp(civilCategoryIndex, uuid) {
				this.$router.push({
					path: '/civil-app',
					query: {
						civilCategoryIndex: civilCategoryIndex,
						secCategoryUuid: uuid,
					}
				});
			},
			//跳转到新闻中心
			toNewsCenter(item) {
				let categoryUuid = ''
				let secCategoryUuid = ''
				if (item.parentUuid != '') {
					categoryUuid = item.parentUuid
					secCategoryUuid = item.uuid
				} else {
					categoryUuid = item.uuid
				}
				this.$router.push({
					name: 'newsCenterApp',
					params: {
						categoryUuid: categoryUuid,
						secCategoryUuid: secCategoryUuid
					}
				});
			},
			//跳转分类（路由）
			toggleNavigation(url) {
				this.$router.push({
					path: url,
					query: {
					}
				});
			},

		}
	}
</script>
<style lang="scss" scoped>
	.contact-us-app {
		width: 100%;
		height: 100%;
	}

	.banner-box {
		width: 100%;
		height: 4.26rem;
		background-image: url(../../../static/image/sitemap/banner-app.png);
		background-size: cover;
		background-repeat: no-repeat;
		position: relative;
		padding-top: 1rem;
		box-sizing: border-box;

		.banner-box-shadow {
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
			background: rgba(0, 0, 0, 0.2);
			z-index: 1;
		}

		.banner-title-box {
			width: 4.4rem;
			height: 1.36rem;
			margin: 0 auto;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			position: relative;
			z-index: 2;
			margin-top: 1.06rem;

			.banner-title-top {
				width: 2rem;
				border-top: 0.04rem solid #FFFFFF;
				opacity: 1;
			}

			.banner-title {
				width: 100%;
				height: 0.56rem;
				font-size: 0.56rem;
				font-family: PingFangSC-Medium;
				font-weight: 500;
				line-height: 0.60rem;
				color: #FFFFFF;
				letter-spacing: 0.56px;
				opacity: 1;
				text-align: center;
			}

			.banner-title-bottom {
				width: 2rem;
				border-top: 0.04rem solid #FFFFFF;
				opacity: 1;
				margin-left: auto;
			}
		}

	}

	.introduction-box {
		width: 100%;

		.introduction-middle {
			height: 100%;
			margin: 0 auto;
			padding: 0.6rem 0rem 1rem 0rem;
			box-sizing: border-box;

			.introduction-padding {
				padding: 0rem 0.48rem 0rem 0.48rem;
				box-sizing: border-box;
			}

			.introduction-top-box {
				display: flex;
				align-items: center;
				margin: 0 auto;

				.top-title {
					height: 0.5rem;
					font-size: 0.36rem;
					font-family: PingFangSC-Medium;
					line-height: 0.5rem;
					color: #333333;
					letter-spacing: 0.36px;
					opacity: 1;
				}

				.top-left {
					.top-left-text {
						height: 0.34rem;
						font-size: 0.28rem;
						font-family: Roboto;
						font-weight: bold;
						line-height: 0.34rem;
						color: #999999;
						letter-spacing: 0.28px;
						opacity: 1;
						margin-left: 0.28rem;
					}

					.top-left-line {
						width: 4.94rem;
						border-top: 0.02rem solid #DFDFDF;
						opacity: 1;
						margin-top: 0.04rem;
						margin-left: 0.12rem;
					}
				}
			}

			.sitemap-box {
				margin-top: 0.4rem;

				.sitemap-title {
					height: 0.44rem;
					font-size: 0.32rem;
					font-family: PingFangSC-Medium;
					line-height: 0.44rem;
					color: #333333;
					letter-spacing: 0.32px;
					opacity: 1;
				}

				.sitemap-text-box {
					display: flex;
					flex-wrap: wrap;

					.sitemap-text {
						width: 3.27rem;
						height: 0.4rem;
						font-size: 0.28rem;
						font-family: PingFangSC-Regular;
						font-weight: 400;
						line-height: 0.4rem;
						color: #333333;
						letter-spacing: 0.28px;
						opacity: 1;
						margin-top: 0.32rem;
						//margin-right: 0.48rem;
					}

					.sitemap-text:hover {
						color: #AB0101;
					}
				}
			}

			.sitemap-line {
				width: 6.14rem;
				height: 0.01rem;
				background-image: linear-gradient(to right, #DFDFDF 0%, #DFDFDF 50%, transparent 50%);
				background-size: 15px 2px;
				background-repeat: repeat-x;
				opacity: 1;
				margin: 0 auto;
				margin-top: 0.4rem;
			}

		}

	}
</style>
