<template>
	<div>
		<!-- small-box 小分页样式 -->
		<div v-if="small">
			<ul v-if="totalPage >= 7" class="small-box">
				<li @click="handleClick($event)" :class="[{forbid: current === 1}, 'prev']">上一页</li>
				<li @click="handleClick($event)" v-if="currentPage<=1" :class="{active:current === 1}">1</li>
				<li @click="handleClick($event)" v-if="currentPage != 1" class="default">...</li>
				<li @click="handleClick($event)" v-for="val in pagesSmall" :key="val" :class="{active:current === val}">{{val}}</li>
				<li @click="handleClick($event)" v-if="currentPage != totalPage" class="default">...</li>
				<li @click="handleClick($event)" style="margin-right: 0;" :class="[{forbid: current === totalPage}, 'next']">
					下一页</li>
			</ul>
			<ul v-else class="small-box">
				<li @click="handleClick($event)" v-for="val in pagesSmall" :key="val" :style="{marginRight:val == totalPage?'0px':'20px'}" :class="{active: current === val}">{{val}}</li>
			</ul>
		</div>
		<div v-else>
			<ul v-if="totalPage >= 7" >
				<li @click="handleClick($event)" v-if="current!=1" :class="[{forbid: current === 1}, 'prev']">上一页</li>
				<li @click="handleClick($event)" :class="{active:current === 1}">1</li>
				<li @click="handleClick($event)" v-if="1 !== pages[0] - 1" class="default">...</li>
				<li @click="handleClick($event)" v-for="val in pages" :key="val" :class="{active:current === val}">{{val}}</li>
				<li @click="handleClick($event)" v-if="pages[pages.length - 1] !== totalPage - 1" class="default">...</li>
				<li @click="handleClick($event)" :class="{active:current === totalPage}">{{totalPage}}</li>
				<li @click="handleClick($event)" v-if="current!=totalPage" :class="[{forbid: current === totalPage}, 'next']">
					下一页</li>
			</ul>
			<ul v-else>
				<li @click="handleClick($event)" v-if="current!=1&&totalPage>5" :class="[{forbid: current === 1}, 'prev']">上一页</li>
				<li @click="handleClick($event)" v-for="val in pages" :key="val" :class="{active: current === val}">{{val}}</li>
				<li @click="handleClick($event)" v-if="current!=totalPage&&totalPage>5" :class="[{forbid: current === totalPage}, 'next']">
					下一页</li>
			</ul>
		</div>
	</div>
</template>
<script>
	export default {
		name: 'pagination',
		props: {
			//总条数
			total: {
				required: true,
				type: Number
			},
			//分页条数
			pageSize:{
				type: Number,
				default: 10
			},
			//小分页样式
			small: {
				type:Boolean,
				default: false
			},
			//开始页数
			currentPage: {
				type: Number,
				default: 1
			}
		},
		data() {
			return {
				current: 0,
				totalPage:0,//总页数
			}
		},
		created() {
			this.current = this.currentPage
		},
		watch: {
			currentPage() {
				var _this = this
				_this.current = _this.currentPage
			},
			'total': {
				  immediate: true,
			      handler (val, oldVal) {
			        this.totalPage=Math.ceil(this.total/this.pageSize)
			      }
			}
		},
		computed: {
			pagesSmall() {
				// 中间五个数
				// 当current > 4 && current < totalPage - 3时，发现每次加减页码，中间的五个数都会变，而当current < 4|| current >= totalPage - 3时，中间的五个数都是稳定的
				var _this = this,
					ct = _this.current,
					tp = _this.totalPage
				if (tp >= 7) {
					//  页数超过6页
					if (ct <= 1) {
						return [2]
					}else if (ct < tp) {
						return [ct]
					}else if (ct = tp) {
						return [ct-1,ct]
					}else {
						return [tp - 5]
					}
				} else {
					// 页数少于7页，直接全部渲染出来
					return [1, 2, 3, 4, 5, 6].filter(item => item <= tp)
				}
			},
			pages() {
				// 中间五个数
				// 当current > 4 && current < totalPage - 3时，发现每次加减页码，中间的五个数都会变，而当current < 4|| current >= totalPage - 3时，中间的五个数都是稳定的
				var _this = this,
					ct = _this.current,
					tp = _this.totalPage
				if (tp >= 7) {
					//  页数超过6页
					if (ct > 3 && ct < tp - 3) {
						return [ct - 2, ct - 1, ct, ct + 1, ct + 2]
					} else if (ct <= 3) {
						return [2, 3, 4, 5, 6]
					} else {
						return [tp - 5, tp - 4, tp - 3, tp - 2, tp - 1]
					}
				} else {
					// 页数少于7页，直接全部渲染出来
					return [1, 2, 3, 4, 5, 6].filter(item => item <= tp)
				}
			}
		},
		methods: {
			handleClick(e) {
				var el = e.target,
					_this = this
				//已点击
				if (el.classList.contains('active')) {
					return
				}
				if (el.classList.contains('prev')) {
					if (!el.classList.contains('forbid')) {
						_this.current--
						_this.$emit('current-change', _this.current)
					}
				} else if (el.classList.contains('next')) {
					if (!el.classList.contains('forbid')) {
						_this.current++
						_this.$emit('current-change', _this.current)
					}
				} else if (el.classList.contains('default')) {
					// 省略号什么也不触发
				} else {
					_this.current = Number(el.innerHTML)
					_this.$emit('current-change', _this.current)
				}
			}
		}
	}
</script>
<style lang="scss" scoped>
	//去除ios 原生点击效果
	li:not(.default):not(.prev):not(.next){
		-webkit-tap-highlight-color: rgba(255, 0, 0, 0) !important;
	}
	.active {
			background: #AB0101;
			color: #FFFFFF;
			border-color: #AB0101 !important;
	}
	.forbid {
		cursor: no-drop;
	}
	.default {
		cursor: default;
		//padding: 0 25px;
		border: none;
		background: transparent;
	}
	.prev,.next {
		//padding: 0 11px;
	}

	ul {
		display: flex;
		justify-content: center;
		align-items: center;
		list-style-type: none;
		font-size: 0;
	}

	li {
		border: 1px solid rgba(171, 1, 1, 0);
		position: relative;
		margin-right: 20px;
		// padding: 0 25px;
		width: 59px;
		height: 30px;
		background: #FFFFFF;
		opacity: 1;
		border-radius: 4px;
		overflow: hidden;

		font-size: 14px;
		font-family: SourceHanSansCN-Normal;
		font-weight: 400;
		line-height: 30px;
		color: #333333;
		text-align: center;
		white-space: nowrap;
		cursor: pointer;
		user-select: none;
		box-sizing: border-box;
	}

	
	//小型分页样式 重写上面样式
	.small-box{
		li{
			border: 1px solid #E3E3E3;
			//padding: 0 11px;
			width: 40px;
			height: 31px;
			line-height: 31px;
			font-size: 15px;
		}
		.prev,.next {
			width: 59px;
		}
		
	}
	@import "./css/pc.scss";
	@import "./css/ipad-pro.scss";
	@import "./css/ipad.scss";
</style>
