<template>
	<div class="professional-field">
		<div class="placeholder"></div>
		<!-- start 专业领域 -->
		<div class="introduction-box">
			<div class="introduction-middle">
				<div class="field-details">
					<div class="field-navigation">
						<div @click="toProfessional(item.uuid)" :style="{marginTop: index == 0?'0px':'30px'}"
							class="navigation-box" v-for="(item,index) in fieldList">
							<img :style="{opacity: fieldUuid == item.uuid?'1':'0'}"
								src="../../../static/image/professional-field/navigation.png" />
							<div :style="{color: fieldUuid == item.uuid?'#AB0101':'#666666'}" class="navigation-text">
								{{item.fieldName}}
							</div>
						</div>
					</div>
					<div class="field-content">
						<div class="content-box">
							<div class="content-title">{{fieldDetails.fieldName}}</div>
							<div class="content-line"><img src="../../../static/image/professional-field/flag.png" />
							</div>
							<div class="content-text">{{fieldDetails.fieldIntro}}</div>
						</div>
						<div class="main-service">
							<div class="service-title">主要服务</div>
							<div class="service-line"><img
									src="../../../static/image/professional-field/service-flag.png" /></div>
							<div class="service-box"
								v-for="(item,index) in fieldDetails.professionalFieldServiceItemList">
								<div class="service-box-title" v-if="item.serviceTitle !=''">{{item.serviceTitle}}</div>
								<div class="service-box-text">{{item.serviceItem}}</div>
								<div class="service-box-line"></div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
		<!-- end 专业领域 -->
	</div>
</template>

<script>
	import {
		getProfessionalFieldList,
		getProfessionFieldDetail
	} from '../../apis/openapi.js';
	import UTCUtil from '../../../static/js/UTCUtil.js';
	export default {
		metaInfo: function() {
			return this.getMetaInfo
		},
		components: {
		},
		data() {
			return {
				fieldUuid:'',
				fieldDetails: {},
				fieldList: [],
				fieldMeta: ['bankingFinance','administrativeLitigation','laborEmployment','intellectualProperty','professionalCriminal','comprehensiveBusiness','environmentResources','maritimeMaritime','litigationArbitration','constructionProject','housework','corporateAccounts'],
			}
		},
		computed: {
			getMetaInfo: function() {
				if(this.fieldDetails.fieldName == '银行与金融'){
					return this.$VueMetaInfo['bankingFinance']
				}else if(this.fieldDetails.fieldName == '行政诉讼'){
					return this.$VueMetaInfo['administrativeLitigation']
				}else if(this.fieldDetails.fieldName == '劳动与雇佣'){
					return this.$VueMetaInfo['laborEmployment']
				}else if(this.fieldDetails.fieldName == '知识产权'){
					return this.$VueMetaInfo['intellectualProperty']
				}else if(this.fieldDetails.fieldName == '刑事'){
					return this.$VueMetaInfo['professionalCriminal']
				}else if(this.fieldDetails.fieldName == '公司综合类业务'){
					return this.$VueMetaInfo['comprehensiveBusiness']
				}else if(this.fieldDetails.fieldName == '环境与资源'){
					return this.$VueMetaInfo['environmentResources']
				}else if(this.fieldDetails.fieldName == '海事海商'){
					return this.$VueMetaInfo['maritimeMaritime']
				}else if(this.fieldDetails.fieldName == '民商事诉讼与仲裁'){
					return this.$VueMetaInfo['litigationArbitration']
				}else if(this.fieldDetails.fieldName == '房地产与建设工程'){
					return this.$VueMetaInfo['constructionProject']
				}else if(this.fieldDetails.fieldName == '家事业务及私人财富管理'){
					return this.$VueMetaInfo['housework']
				}else if(this.fieldDetails.fieldName == '企业应收账款服务'){
					return this.$VueMetaInfo['corporateAccounts']
				}else{
					return this.$VueMetaInfo['professionalField']
				}
			}
		},
		// 改变参数且不刷新页面
		watch: {
			// 监听参数是否发生变化
			$route: {
				handler() {
					if(this.$route.params.uuid){
						this.fieldUuid = this.$route.params.uuid
						this.getProfessionFieldDetail(this.$route.params.uuid)
					}
				},
				//深度监听，同时也可监听到param参数变化
				deep: true,
			}
		},
		mounted() {
			if (this.$route.params.uuid) {
				this.fieldUuid = this.$route.params.uuid
				this.getProfessionFieldDetail(this.$route.params.uuid)
			}
			this.getProfessionalFieldList()

		},
		methods: {
			//查询专业领域信息
			getProfessionalFieldList() {
				let params = {
					page: 1,
					rows: 100
				}
				getProfessionalFieldList(params).then(res => {
					if (res.data.success == 'true') {
						this.fieldList = res.data.data.data
					}
				});
			},
			getProfessionFieldDetail(uuid) {
				getProfessionFieldDetail({
					uuid: uuid
				}).then(res => {
					if (res.data.success == 'true') {
						this.fieldDetails = res.data.data
						this.fieldUuid = uuid
						console.log(this.fieldDetails);
					}
				});
			},
			//跳转到专业邻域(该页面跳转是为了刷新路由传值)
			toProfessional(uuid) {
				this.$router.push('/professional-field-details/'+uuid)
			},

		}
	}
</script>
