		// 改变参数且不刷新页面
		watch: {
			// 监听参数是否发生变化
			$route: {
				handler() {
					if (this.$route.query.civilCategoryIndex) {
						this.civilCategoryIndex = this.$route.query.civilCategoryIndex
						this.params.secCategoryUuid = this.$route.query.secCategoryUuid
					} else {
						this.civilCategoryIndex = -1
						this.params.secCategoryUuid = ''
					}
					this.params.page = 1
					this.newsList()
					console.log(this.civilCategoryIndex);
				},
				//深度监听，同时也可监听到param参数变化
				deep: true,
			},
		},
		mounted() {
			if (this.$store.state.categoryInfo) {
				let categoryList = this.$store.state.categoryInfo
				this.secondCategoryList = categoryList.find((item) => item.categoryName == '民商事').children
				this.params.categoryUuid = categoryList.find((item) => item.categoryName == '民商事').uuid
			}
			if (this.$route.query.civilCategoryIndex) {
				this.civilCategoryIndex = this.$route.query.civilCategoryIndex
				this.params.secCategoryUuid = this.$route.query.secCategoryUuid
			} else {
				this.civilCategoryIndex = -1
				this.params.secCategoryUuid = ''
			}
			this.newsList()
		},
		methods: {
			//重置到首页
			reset() {
				this.civilCategoryIndex = -1
				this.civilCategoryStyle = 0 //首页分类样式下标
				this.serviceModelStyle = 0 //首页服务模式样式下标
				this.disputeIndex = 0 //合同纠纷下标
				this.inheritanceIndex = 0 //婚姻遗产下标
				this.disputesIndex = 0 //侵权纠纷下标
				this.laborIndex = 0 //劳动纠纷下标
				this.equityTypeIndex = 0 //股权服务内容下标
				document.body.scrollTop = 0; // chrome
				document.documentElement.scrollTop = 0; // firefox
			},
			handleMouseEnter(index) {
				this.civilCategoryStyle = index
			},
			handleMouseLeave(index) {
				//this.civilCategoryStyle = -1
			},
			//查询新闻列表
			newsList() {
				newsList(this.params).then(res => {
					if (res.data.success == 'true') {
						this.newsAllList = res.data.data.data
						this.totalCount = res.data.data.total
						this.newsAllList.forEach(item => {
							if (item.publishTime) {
								item.years = UTCUtil.showLocalDate(item.publishTime, 'YYYY/mm');
								item.day = UTCUtil.showLocalDate(item.publishTime, 'dd');
							}
						})
					}
				});
			},
			toNewsDetails(uuid) {
				this.$router.push({
					path: '/news-center-app',
					query: {
						newsUuid: uuid,
					}
				});
			},
			//分页切换
			handleCurrentChange(val) {
				this.params.page = val;
				this.newsList()
			},
			//跳转到民商事(该页面跳转是为了刷新路由传值)
			toCivilApp(civilCategoryIndex, uuid) {
				this.$router.push({
					path: '/civil-app',
					query: {
						civilCategoryIndex: civilCategoryIndex,
						secCategoryUuid: uuid,
					}
				});
			},
			//跳转分类（路由）
			toggleNavigation(url) {
				this.$router.push({
					path: url,
					query: {}
				});
				//this.$forceUpdate()
			},


		}
	}
