					</div>
					<div class="message-text">有容事务所全球办公室向您发出诚挚邀请，<br />

						<br />如果您希望有新的挑战与突破，<br />

						<br />我们期待您参与有容的发展变革，一同叩开新格局下律所的大门。<br />

						<br />有容非常注重优秀人才的选拔和培养，统一的入职考核，针对员工的不同培训，如新员工培训、律师业务培训等。<br />

						<br />我们希望来到有容的每个人都能有所收获，每一天都能有所成长，期待您的加入。
