							<div class="recruitment-title">合伙人</div>
							<div class="recruitment-text">
								<div class="recruitment-require">职位要求</div>
								<div class="require-content">1、统招法学专业本科以上学历，有硕士、博士及留学背景者优先;<br />

									<br />2、具有律师执业证且有5年以上执业经历，在国内外大型律师事务所担任同等岗位优先;<br />

									<br />3、认同有容的文化与发展理念;<br />

									<br />4、擅长资本市场、银行金融业务、税务筹划、跨境业务、争议解决、环境保护、文化体育与TMT、网络安全和数据保护等专业领域法律服务优先;<br />

									<br />5、拥有广泛的业务渠道及良好的客户资源，具备优秀的团队管理能力，乐于合作发展。
								</div>
							</div>
						</div>
