								<div class="require-content">1、统招法学专业本科以上学历，有硕士、博士及留学背景者优先;<br />

									<br />2、具有律师执业证且有2年以上的执业经历，在国内外大型律师事务所工作背景者优先;3、认同有容的文化与发展理念，认同有容的管理;<br />

									<br />4、有较强的业务开拓能力;<br />

									<br />5、有一定的业务专长，擅长资本市场、银行金融业务、税务筹划、跨境业务、争议解决、环境保护、文化体育与TMT、网络安全和数据保护等专业领域法律服务优先。
								</div>
							</div>
