								<div class="require-content">1、统招法学专业硕士以上学历，博士及有留学背景者优先（优秀的本科毕业生可考虑);<br />

									<br />2、通过国家司法考试并获得A证;<br />

									<br />3、具备较强的文字功底;<br />

									<br />4、沟通能力强，具有强烈的责任心;<br />

									<br />5、热爱律师工作，能够承受较大的工作压力，积极主动，有进取心。<br />
								</div>
							</div>
