<script>
	import {
		getContactUsList,
		getProfessionalFieldList
	} from '../../apis/openapi.js';
	import UTCUtil from '../../../static/js/UTCUtil.js';
	export default {
		metaInfo:window.$VueMetaInfo['contactUs'],
		components: {},
		data() {
			return {
				fieldList: [],
				enterpriseInfo: {}, //企业信息
				longitude: '', //精度
				latitude: '', //维度
				categoryList:[],
				civilUuid:'',
				civilCategoryList:[],
				headerList:[
					{
						name:'首页',
						url: '/'
					},
					{
						name:'应收账款',
						url: '/accounts-receivable'
					},
					{
						name:'法律顾问',
						url: '/legal-counsel'
					},
					{
						name:'刑事',
						url: '/criminal'
					},
					{
						name:'民商事',
						url: '/civil'
					},
					{
						name:'专业领域',
						url: '/professional-field'
					},
					{
						name:'法律知识',
						url: '/news-center'
					},
					{
						name:'联系我们',
						url: '/contact-us'
					},
				],
			}
		},
		mounted() {
			if (this.$store.state.categoryInfo) {
				this.categoryList = this.$store.state.categoryInfo
				this.civilUuid = this.categoryList.find((item)=>item.categoryName=='民商事').uuid
				this.civilCategoryList = this.categoryList.find((item)=>item.categoryName=='民商事').children
			} else {
				this.getCategoryList()
			}
			if (this.$store.state.companyInfo) {
				this.enterpriseInfo = this.$store.state.companyInfo
			} else {
				this.getContactUsList()
			}
			if (this.$store.state.headFieldList) {
				this.fieldList = this.$store.state.headFieldList
			} else {
				this.getProfessionalFieldList()
			}
			if (this.$route.query.id) {
				setTimeout(() => {
					this.goAuchor('#' + this.$route.query.id)
				}, 300)
			}
		},
		methods: {
			goAuchor(id) {
				document.querySelector(id).scrollIntoView({
					behavior: "smooth"
				});
				// document.querySelector(id).scrollIntoView(true)
				// var auchor = this.$el.querySelector(id)
				// document.body.scrollTop = auchor.offsetTop
			},
			//查询分类
			getCategoryList() {
				let params = {
					page: 1,
					rows: 9999,
					categoryName: '',
					parentUuid: '0',
					categoryState: ''
				}
				getCategoryList(params).then(res => {
					if (res.data.success == 'true') {
						this.categoryList = res.data.data.data
						var categoryInfo = res.data.data.data
						this.$store.commit('modifyCategoryInfo', categoryInfo);
						localStorage.setItem('categoryInfo', JSON.stringify(categoryInfo))
						this.civilCategoryList = this.categoryList.find((item)=>item.categoryName=='民商事').children
					}
				})
			},
			//查询企业信息
			getContactUsList() {
				getContactUsList({}).then(res => {
					if (res.data.success == 'true') {
						this.enterpriseInfo = res.data.data
						this.getAddress()
					}
				});
			},
			//查询专业领域信息
			getProfessionalFieldList() {
				let headFieldList = []
				let params = {
					page: 1,
					rows: 100
				}
				getProfessionalFieldList(params).then(res => {
					if (res.data.success == 'true') {
						this.fieldList = res.data.data.data
						this.fieldList.forEach((item, key) => {
							headFieldList.push({
								fieldName: item.fieldName,
								sort: item.sort,
								uuid: item.uuid,
							})
						})
						this.$store.commit('modifyHeadFieldList', headFieldList);
						localStorage.setItem('headFieldList', JSON.stringify(headFieldList))
					}
				});
			},
			//百度地图开始
			getAddress() {
				this.$jsonp('http://api.map.baidu.com/geocoding/v3/', {
					address: this.enterpriseInfo.address,
					output: 'json',
					ak: 'P1YyaLi1qV5gHGsiNNnGlOYpSvNlOgfK'
				}).then(json => {
					// 得到我们想要的内容
					console.log(json)
					this.longitude = json.result.location.lng
					this.latitude = json.result.location.lat
					this.createMap();
				}).catch(err => {
					console.log(err)
				})

			},
			createMap() {
				/* eslint-disable */
				// 创建Map实例
				var map = new BMap.Map("map")
				// 初始化地图,设置中心点坐标和地图级别
				map.centerAndZoom(new BMap.Point(this.longitude, this.latitude), 50)
				//添加地图类型控件
				map.addControl(new BMap.MapTypeControl({
					mapTypes: [BMAP_NORMAL_MAP, BMAP_HYBRID_MAP]
				}))
				//设置标注的图标
				var icon = new BMap.Icon(require("../../../static/image/contact-us/mark.png"), new BMap.Size(100, 100));
				//设置标注的经纬度
				var marker = new BMap.Marker(new BMap.Point(this.longitude, this.latitude), {
					icon: icon
				});
				//把标注添加到地图上
				map.addOverlay(marker);
				// 设置地图显示的城市 此项是必须设置的
				map.setCurrentCity("崂山区52号宗英大厦26楼2602")
				//开启鼠标滚轮缩放
				map.enableScrollWheelZoom(true)
				/* eslint-enable */
			},
			//百度地图结束
			//跳转到民商事
			toCivil(civilCategoryIndex, categoryUuid,secCategoryUuid) {
				this.$router.push({
					path: '/civil',
					query: {
						civilCategoryIndex: civilCategoryIndex,
						categoryUuid: categoryUuid,
						secCategoryUuid:secCategoryUuid
					}
				});
			},
			//跳转到专业邻域
			toProfessional(fieldIndex, uuid) {
				this.$router.push({
					path: '/professional-field',
					query: {
						fieldIndex: fieldIndex,
						fieldUuid: uuid,
					}
				});
			},
			//跳转分类（路由）
			toggleNavigation(url) {
				this.$router.push({
					path: url,
					query: {
					}
				});
			},

		}
	}
</script>
