<template>
	<div class="civil">
		<!-- 有banner的头部导航 -->
		<div v-if="civilCategoryIndex == -1">
			<div class="header-shadow"></div>
			<!-- start banner -->
			<div class="banner-box">
				<div class="banner-box-shadow"></div>
				<div class="banner-title-box">
					<div class="banner-title-top"></div>
					<div class="banner-title">民商事</div>
					<div class="banner-title-bottom"></div>
				</div>
			</div>
			<!-- end banner -->
		</div>
		<!-- 没有banner的头部导航 -->
		<div v-else class="placeholder"></div>

		<!-- start 民商事 -->
		<div class="introduction-box">
			<div class="introduction-middle" :style="{ paddingTop: civilCategoryIndex == -1 ? '50px' : '70px' }">
				<!--start 首页导航栏 -->
				<div v-if="civilCategoryIndex == -1" class="introduction-top-box">
					<div class="top-title">民商事</div>
					<div class="top-left">
						<div class="top-left-navmenu">
							<div class="top-left-text">CIVIL AND COMMERCIAL MATTERS</div>
						</div>
						<div class="top-left-line"></div>
					</div>
				</div>
				<!--end 首页导航栏 -->
				<!--start 详情页	导航栏 -->
				<div class="navmenu-box" v-else>
					<div style="margin-left: 0" class="navmenu-details" @click="toggleNavigation('/')">首页</div>
					<img src="../../../static/image/professional-field/nabar.png" />
					<div class="navmenu-details" @click="toCivil(-1,'0')">民商事</div>
					<img src="../../../static/image/professional-field/nabar.png" />
					<div v-for="(item, index) in civilCategoryList" v-if="civilCategoryIndex == index"
						class="navmenu-details" style="color: #999999">
						{{ item.name }}
					</div>
				</div>
				<!--end 详情页	导航栏 -->
				<!--start 合同纠纷 -->
				<contractualDispute :civilCategoryIndex="civilCategoryIndex" :scroll="scroll"
					:secCategoryUuid="secondCategoryList[civilCategoryIndex].uuid" v-if="civilCategoryIndex == 0">
				</contractualDispute>
				<!--end 合同纠纷 -->
				<!--start 婚姻遗产 -->
				<maritalInheritance :civilCategoryIndex="civilCategoryIndex" :scroll="scroll"
					:secCategoryUuid="secondCategoryList[civilCategoryIndex].uuid" v-else-if="civilCategoryIndex == 1">
				</maritalInheritance>
				<!--end 婚姻遗产 -->
				<!--start 侵权纠纷-->
				<infringementDisputes :civilCategoryIndex="civilCategoryIndex" :scroll="scroll"
					:secCategoryUuid="secondCategoryList[civilCategoryIndex].uuid" v-else-if="civilCategoryIndex == 2">
				</infringementDisputes>
				<!--end 侵权纠纷 -->
				<!--start 劳动纠纷 -->
				<laborDispute :civilCategoryIndex="civilCategoryIndex" :scroll="scroll"
					:secCategoryUuid="secondCategoryList[civilCategoryIndex].uuid" v-else-if="civilCategoryIndex == 3">
				</laborDispute>
				<!--end 劳动纠纷 -->
				<!--start 股权 -->
				<equity :civilCategoryIndex="civilCategoryIndex" :scroll="scroll" v-else-if="civilCategoryIndex == 4"
					:secCategoryUuid="secondCategoryList[civilCategoryIndex].uuid">
				</equity>
				<!--end 股权 -->
				<!--start 首页 -->
				<home-page v-else></home-page>
				<!--end 首页 -->
				<!--start 新闻列表 -->
				<news-list-component ref="newsListComponent"></news-list-component>
				<!--end  新闻列表 -->
			</div>
		</div>
		<!-- end 民商事 -->
	</div>
</template>

<script>
	import {
		getContactUsList,
		newsList,
		getNewsDetail,
		getCategoryList,
	} from "../../apis/openapi.js";
	import UTCUtil from "../../../static/js/UTCUtil.js";
	import pagination from "@/components/pagination";
	import contractualDispute from "./components/contractual-dispute";
	import maritalInheritance from "./components/marital-inheritance";
	import infringementDisputes from "./components/infringement-disputes";
	import laborDispute from "./components/labor-dispute";
	import equity from "./components/equity";
	import homePage from "./components/home-page.vue";
	import newsListComponent from "./components/news-list.vue";
	export default {
		metaInfo: function() {
			return this.getMetaInfo
		},
		components: {
			pagination,
			contractualDispute,
			maritalInheritance,
			infringementDisputes,
			laborDispute,
			equity,
			homePage,
			newsListComponent,
		},
		data() {
			return {
				scroll: 0,
				totalCount: 100, //分页用的总数
				params: {
					page: 1,
					rows: 6,
					categoryUuid: "",
				},
				newsAllList: [],
				categoryList: [],
				civilCategoryIndex: -1, //首页分类下标
				civilCategoryStyle: 0, //首页分类样式下标
				serviceModelStyle: 0, //首页服务模式样式下标
				disputeIndex: 0, //合同纠纷下标
				inheritanceIndex: 0, //婚姻遗产下标
				disputesIndex: 0, //侵权纠纷下标
				laborIndex: 0, //劳动纠纷下标
				equityTypeIndex: 0, //股权服务内容下标
				civilCategoryList: [{
						name: "合同纠纷",
					},
					{
						name: "婚姻遗产",
					},
					{
						name: "侵权纠纷",
					},
					{
						name: "劳动纠纷",
					},
					{
						name: "股权",
					},
				],
				secondCategoryList: [],
			};
		},
		computed: {
			isScroll() {
				return this.$store.state.scroll; //需要监听的数据
			},
			getMetaInfo: function() {
				if(this.civilCategoryIndex == 0){
					return this.$VueMetaInfo['contractualDispute']
				}else if(this.civilCategoryIndex == 1){
					return this.$VueMetaInfo['maritalInheritance']
				}else if(this.civilCategoryIndex == 2){
					return this.$VueMetaInfo['infringementDisputes']
				}else if(this.civilCategoryIndex == 3){
					return this.$VueMetaInfo['laborDispute']
				}else if(this.civilCategoryIndex == 4){
					return this.$VueMetaInfo['equity']
				}else{
					return this.$VueMetaInfo['civil']
				}
			}
		},
		// 改变参数且不刷新页面
		watch: {
			// 监听参数是否发生变化
			$route: {
				handler() {
					if (this.$route.params.civilCategoryIndex) {
						this.civilCategoryIndex = this.$route.params.civilCategoryIndex;
					} else {
						this.civilCategoryIndex = -1;
					}
				},
				//深度监听，同时也可监听到param参数变化
				deep: true,
			},
			//监听页面是否滚动
			isScroll(newVal, oldVal) {
				this.scroll = newVal;
			},
		},
		mounted() {
			if (this.$store.state.categoryInfo) {
				let categoryList = this.$store.state.categoryInfo;
				this.secondCategoryList = categoryList.find((item) => item.categoryName == "民商事").children;
				this.params.categoryUuid = categoryList.find((item) => item.categoryName == '民商事').uuid
				//this.newsList()
			}
			if (this.$route.params.civilCategoryIndex) {
				this.civilCategoryIndex = this.$route.params.civilCategoryIndex;
			}
		},
		methods: {
			chooseDispute(index) {
				//this.disputeIndex = index
				this.goAuchor("#contract" + index);
			},
			goAuchor(id) {
				document.querySelector(id).scrollIntoView({
					behavior: "smooth",
				});
			},
			//重置到首页
			reset() {
				this.civilCategoryIndex = -1;
				this.civilCategoryStyle = 0; //首页分类样式下标
				this.serviceModelStyle = 0; //首页服务模式样式下标
				this.disputeIndex = 0; //合同纠纷下标
				this.inheritanceIndex = 0; //婚姻遗产下标
				this.disputesIndex = 0; //侵权纠纷下标
				this.laborIndex = 0; //劳动纠纷下标
				this.equityTypeIndex = 0; //股权服务内容下标
				document.body.scrollTop = 0; // chrome
				document.documentElement.scrollTop = 0; // firefox
			},
			handleMouseEnter(index) {
				this.civilCategoryStyle = index;
			},
			handleMouseLeave(index) {
				//this.civilCategoryStyle = -1
			},
			ServiceModeEnter(index) {
				this.serviceModelStyle = index;
			},
			ServiceModeLeave(index) {
				//this.serviceModelStyle = -1
			},
			//查询新闻列表
			newsList() {
				newsList(this.params).then((res) => {
					if (res.data.success == "true") {
						this.newsAllList = res.data.data.data;
						this.totalCount = res.data.data.total;
						this.newsAllList.forEach((item) => {
							if (item.publishTime) {
								item.years = UTCUtil.showLocalDate(item.publishTime, "YYYY-mm");
								item.day = UTCUtil.showLocalDate(item.publishTime, "dd");
							}
						});
					}
				});
			},
			//分页切换
			handleCurrentChange(val) {
				this.params.page = val;
				this.newsList();
			},
			//跳转到民商事(该页面跳转是为了刷新路由传值)
			toCivil(civilCategoryIndex, secCategoryUuid) {
				this.$router.push('/civil/' + civilCategoryIndex + '/' + secCategoryUuid)
			},
			//跳转分类（路由）
			toggleNavigation(url, index) {
				this.$router.push({
					path: url,
					query: {},
				});
				//this.$forceUpdate()
			},
		},
	};
</script>
