						<div class="top-left-line"></div>
					</div>
				</div>
				<div class="field-box" v-if="fieldList.length>0">
					<div @click="toDetails(item.uuid)" :style="{marginRight:index%2 == 1?'0px':'76px'}"
						class="professional-box" v-for="(item,index) in fieldList">
						<img src="../../../static/image/professional-field/field-arrow.png" />
						<div class="professional-left">
							<div style="display: flex;">
								<div class="professional-top">
									<div class="professional-title">{{item.fieldName}}</div>
									<div class="professional-line"></div>
								</div>
							</div>
							<div class="professional-text">{{item.fieldIntro}}</div>
							<div class="see-details"></div>
						</div>
					</div>
				</div>
			</div>
		</div>
		<!-- end 专业领域 -->
	</div>
</template>

<script>
	import {
		getProfessionalFieldList,
		getProfessionFieldDetail
	} from '../../apis/openapi.js';
	import UTCUtil from '../../../static/js/UTCUtil.js';
	export default {
		metaInfo:window.$VueMetaInfo['professionalField'],
		components: {
		},
		data() {
			return {
				fieldUuid:'',
				fieldDetails: {},
				fieldList: [],
			}
		},
		mounted() {
			this.getProfessionalFieldList()

		},
		methods: {
			//查询专业领域信息
			getProfessionalFieldList() {
				let params = {
					page: 1,
					rows: 100
				}
				getProfessionalFieldList(params).then(res => {
					if (res.data.success == 'true') {
						this.fieldList = res.data.data.data
					}
				});
			},
			toDetails(uuid) {
				this.$router.push('/professional-field-details/'+uuid)
			},

		}
	}
</script>
